.red{
    color:red;
}

.bold{
    font-weight: bold;
}

.Cockpit button{
    color: white;
    font: inherit;
    background-color: green;
    border: 1px solid blue;
    padding: 8px;
    cursor: pointer;

}

.Cockpit button:hover{
    background-color: lightgreen;
    color: black;
}

.Cockpit button.red{
    background-color: red;
}
.Cockpit button.red:hover{
    background-color: salmon;
}


